import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { CanActivate, Router } from '@angular/router';
import { environment } from 'src/environments/environment';
import { ModalInfoComponent } from '../components/modal-info/modal-info.component';
import { OidcUser } from '../models/OidcUser';
import { AuthenticationService } from './authentication.service';

@Injectable({
  providedIn: 'root'
})
export class AppService implements CanActivate {
  ipAdress: string;
  oidcUser: OidcUser;
  user: any;

  constructor(public router: Router, private http: HttpClient, private authService: AuthenticationService, public dialog: MatDialog) { }

  canActivate(): boolean {
		try {
			if (this.authService.isTokenLSExpired()) {
				return true;
			} else {
				this.authService.startAuthentication();
				return false;
			}
		} catch (error) {
      console.error("AppService : " + error);
			return false;
		}
  }

  getIPAddress() {
    return this.http.get<any>(
      environment.apiIpify,
      {},
    );
  }

  getIpStorage() {
    this.ipAdress = localStorage.getItem('ipAdress');
    return this.ipAdress;
  }

  getOidcUser() {
    const oidcUser = localStorage.getItem(
      'oidc.user:' + environment.securityOIDC + ':' + environment.clientOIDC
    );
    if (oidcUser != null && oidcUser !== undefined) {
      this.oidcUser = JSON.parse(oidcUser);
    } else {
      this.oidcUser = {} as OidcUser;
    }
    return this.oidcUser;
  }

  async getUserLocal() {
    this.user = await JSON.parse(
      localStorage.getItem('oidc.user:' + environment.securityOIDC + ':' + environment.clientOIDC)
    );
    return this.user;
  }


  async setIpStorage(ipAdress: string) {
    await localStorage.setItem('ipAdress', ipAdress);
  }

	determinateHttpError(err: any, isLoadingLogout ?: any) {
		this.dialog.closeAll();
		isLoadingLogout = (isLoadingLogout == undefined) ? false : isLoadingLogout;
    if (err.status === 401 || err.status === 403) {
      if (!isLoadingLogout) {
        isLoadingLogout = true;
				this.openDialogInfo('Sesión caducada  !', 'Vuelva a iniciar sesión', 'error');
        setTimeout(() => {
          this.authService.logout();
        }, 2500);
      }
    } else {
      this.openDialogInfo('Ocurrió un error  !', 'Intentelo mas tarde, si el error persiste comuníquese con el soporte', 'error');
    }
	}

	openDialogInfo(title: string, subtitle: any, type: string) {
    const dialogref = this.dialog.open(ModalInfoComponent, {
      width: '350px',
      data: { title, subtitle, type }
    });
  }

  
}
