import { Injectable } from '@angular/core';
import { AuthenticationService } from './authentication.service';
import { HttpInterceptor, HttpRequest, HttpHandler, HttpEvent, HttpErrorResponse } from '@angular/common/http';
import { AppService } from './app.service';
import { Observable, of, throwError } from 'rxjs';
import { ModalInfoComponent } from '../components/modal-info/modal-info.component';
import { MatDialog } from '@angular/material/dialog';
import { catchError } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class TokenInterceptorService implements HttpInterceptor {

  constructor(
    private authenticationService: AuthenticationService,
     private appService: AppService,
     public dialog: MatDialog) {
     //private deviceService: DeviceDetectorService) {
      //this.deviceInfo = this.deviceService.getDeviceInfo();
      //const isMobile = this.deviceService.isMobile();
      //const isTablet = this.deviceService.isTablet();
      //const isDesktopDevice = this.deviceService.isDesktop();
    }

  //deviceInfo = null;
  ipAdress: any;
  token: any;


	private handleAuthError(err: HttpErrorResponse): Observable<any> {
    if (err.status === 401) {
			this.openDialogInfo(
				'Sesión caducada  !',
				'Vuelva a iniciar sesión',
				'error'
			);
      setTimeout(() => {
				this.authenticationService.logoutInterceptor();
      }, 1500);
      return of(err.message);
    }
    return throwError(err);
  }

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    if (req.url.includes('ipify')) {
      const tokenizeReq = req.clone({
      });
      return next.handle(tokenizeReq);
    } else {
      this.token = (this.authenticationService.getToken() !== undefined) ? this.authenticationService.getToken() : '';
      this.ipAdress = (this.appService.getIpStorage() !== null) ? this.appService.getIpStorage() : '';
      const tokenizeReq = req.clone({
        setHeaders: {
          Authorization: 'Bearer ' + this.token,
          Ip: this.ipAdress
        }
      });
      tokenizeReq.headers.set('Ip', this.ipAdress);
			return next.handle(tokenizeReq).pipe(catchError(x=> this.handleAuthError(x)));
    }
	}

	openDialogInfo(title: string, subtitle: any, type: string) {
    const dialogref = this.dialog.open(ModalInfoComponent, {
      width: '350px',
      data: { title, subtitle, type }
    });
    dialogref.afterClosed().subscribe(reponse => {
    });
  }

}
