import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from './../../environments/environment';
import { User, UserManager } from 'oidc-client';
import { getClientSettings } from '../sso.config';
import { Router } from '@angular/router';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class AuthenticationService {

	public user: User = null;
	private manager = new UserManager(getClientSettings());
	constructor(public router: Router, private http: HttpClient) {
		this.manager.getUser().then(user => {
			if (user != null) {
				localStorage.setItem('oidc.user:' + environment.securityOIDC + ':' + environment.clientOIDC, user.toStorageString())
				this.user = user;
			}
		});
	}

	isLoggedIn(): boolean {
		return this.user != null && !this.user.expired;
	}

	getClaims(): any {
		return this.user.profile;
	}

	getUser(): any {
		this.manager.getUser().then(user => {this.user = user;});
	}

	public LoginRedirectHandler = (): Promise<any> => {
		return this.manager.getUser().then((user) => {
			if (user === null || user === undefined) {
				return this.manager.signinRedirect(null);
			}
		});
	}

	startAuthentication(): Promise<void> {
		return this.manager.signinRedirect();
	}

	logout(): void {
		if (this.user == null) {
			localStorage.clear();
			this.router.navigate(['/dashboard']);
		} else {
			localStorage.clear();
			this.manager.signoutRedirect({ id_token_hint: this.user.id_token});
		}
	}

  logoutInterceptor(){
    localStorage.clear();
    this.manager.signoutRedirect({ id_token_hint: this.user.id_token});
  }

	async completeAuthentication(): Promise<void> {
		if (this.user === null || this.user === undefined) {
			return await this.manager.signinCallback().then(
				user => {
					this.user = user;
					if (this.isTokenExpired()) {
							this.logout();
					} else {
						this.router.navigate(['/dashboard']);
					}
				}, err => {
					console.log(err);
					this.logout();
				}
			);
		} else {
			if (this.isTokenExpired()) {
					this.logout();
			} else {
				this.router.navigate(['/dashboard']);
			}
		}
	}

	getToken() {
		this.user = JSON.parse(localStorage.getItem('oidc.user:' + environment.securityOIDC +  ':' + environment.clientOIDC));
		return (this.user != null && this.user !== undefined) ? this.user.access_token : '';
	}

	isTokenExpired(): boolean {
		return this.user.expires_at < new Date().getTime() / 1000;
	}

	isTokenLSExpired(): boolean {
		const sessionStorage = JSON.parse(localStorage.getItem('oidc.user:' + environment.securityOIDC + ':' + environment.clientOIDC));
		if (sessionStorage != null) {
			//return true; // Revisar Ojo
			if (sessionStorage.expires_at < new Date().getTime() / 1000) {
				return false;
			} else {
				return true;
			}
		} else {
			return false;
		}
	}

	isvalidToken() {
		return this.http.get<any>(
			environment.securityOIDC + '/authentication/isvalid'
		);
	}

	getApplications() {
		return this.http.get<any>(
			environment.securityOIDC + '/authentication/applications'
			);
		}

	getRolByApplication(appId: string) {
		return this.http.get<any>(
			environment.securityOIDC + '/authentication/roles?applicationId=' + appId
		);
	}

	getAccessByApplication(appId: string) {
		return this.http.get<any>(
			environment.securityOIDC + '/authentication/access?applicationId=' + appId
		);
	}

	test(appId) {
		return this.http.get(environment.securityOIDC + '/authentication/access?applicationId=' + appId).pipe(
			map(
				(response: any) => {
					return response;
				}
			)
		);
	}

	hasPermission(appId) {
		return this.http.get(environment.securityOIDC + '/authentication/actions?applicationId=' + appId).pipe(
			map(
				(response: any) => {
					return response;
				}
			));
	}

}
